body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.lineHeight {
  padding-top: 24px;
}

@media only screen and (max-width: 767px) {
  .lineHeight {
    padding-top: 0px;
  }
}

.moneyNeeded {
  margin-top: 5px;
  text-align: center;
  font-size: 14px;
}

.golden {
  background-color: rgb(179, 134, 0, 0.15) !important;
}

.golden:hover {
  background-color: rgb(179, 134, 0, 0.25) !important;
}

table {
  text-align: center;
}

tr {
  border: 1px solid #dee2e6;
}

td {
  position: relative;
}

.smallTable {
  width: 50%;
}

.leftBorder {
  border-left: 1px solid rgb(180, 180, 180);
}

.cellWithComment {
  position: relative;
}

.cellComment {
  display: none;
  position: absolute;
  z-index: 2;
  border: 1px;
  background-color: white;
  border-style: solid;
  border-width: 1px;
  padding: 3px;
  top: 38px;
  left: 50px;
  width: 250px;
}

.cellWithComment:hover span.cellComment {
  display: block;
}
